@use "global" as *;

.wp-pagenavi {
	text-align: center;
	margin-top: rem(80);
	color: $accent-color;
	@include mq("md") {
		margin-top: rem(78);
	}
}

.wp-pagenavi .previouspostslink,
.wp-pagenavi .page,
.wp-pagenavi .current,
.wp-pagenavi .nextpostslink {
	border: 1px solid $accent-color;
	padding: rem(6) rem(12);
	margin: 0 rem(6);
	font-family: $price-font-family;
	font-weight: $bold;
}

.wp-pagenavi .previouspostslink {
	margin-right: rem(30);
	margin-left: 0;
	position: relative;
}

.wp-pagenavi .current {
	background-color: $accent-color;
	color: $white;
}

.wp-pagenavi .nextpostslink {
	margin-left: rem(30);
	margin-right: 0;
	position: relative;
}

.wp-pagenavi .larger--pc {
	display: none;
	@include mq("md") {
		display: inline-block;
	}
}

.wp-pagenavi .previouspostslink:hover::after {
	border-top: 1px solid $white;
	border-left: 1px solid $white;
	background-color: $accent-color;
	transition: 0.3s ease;
	opacity: 1;
}

.wp-pagenavi .nextpostslink:hover::after {
	border-bottom: 1px solid $white;
	border-right: 1px solid $white;
	background-color: $accent-color;
	transition: 0.3s ease;
	opacity: 1;
}
.wp-pagenavi a,
.wp-pagenavi span {
	display: inline-block;
}

.wp-pagenavi a:hover,
.wp-pagenavi span:hover {
	color: $white;
	background-color: $accent-color;
	transition: 0.3s ease;
	opacity: 1;
}

.wp-pagenavi.page-campaign__wp-pagenavi {
	margin-top: rem(80);
	@include mq(md) {
		margin-top: rem(86);
	}
}

.wp-pagenavi.single-blog__wp-pagenavi {
	margin-top: rem(40);
	@include mq(md) {
		margin-top: rem(80);
	}
}

.wp-pagenavi.page-voice__wp-pagenavi {
	margin-top: rem(95);
	@include mq(md) {
		margin-top: rem(67);
	}
}
