@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////
$ja-font-family: "Noto Sans JP", sans-serif;
$en-font-family: "Gotu", sans-serif;
$price-font-family: "Lato", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
	font-family: $ja-font-family;
	position: relative;
	z-index: -100;
}

main {
	position: relative;
	z-index: -25;
}

// フォントウェイト
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$accent-color: #408f95;
$white: #fff;
$font-color: #0d2936;

body {
	color: $font-color;
}
